.Content{
    width: 100%;
    height: 100%;
    grid-area: Content;
    box-shadow: 0px 0px 20px 1px black;
    padding: 30px;
    min-height: 100vh;
    height: fit-content;

}

@media (max-width: 769px ) {
    .Content{
        padding: 15px 30px 30px 30px;
        min-height: 100%;
    }
}